<template><section><h1>radio 单选框</h1>
<hr>
<p>单选框通常用于一组选项中，只能单独选择一项的情况。</p>
<h2>何时使用</h2>
<p>用于在多个备选项中选中单个状态。</p>
<p>和 Select 的区别是，Radio 所有选项默认可见，方便用户在比较中选择，因此选项不宜过多。</p>
<h2>基本单选框</h2>
<p>最简单的用法。
需要设置v-model绑定变量，选中意味着变量的值为相应 Radio label属性的值，label可以是String或者Number。</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-radio v-model="radio" label="1">单选1</cat-radio>
<cat-radio v-model="radio" label="2">单选2</cat-radio>

<p>你喜欢吃啥</p>
<cat-radio v-model="radio2" label="1">黄焖鸡</cat-radio>
<cat-radio v-model="radio2" label="2">沙县小吃</cat-radio>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span>&gt;</span>单选1<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>单选2<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>你喜欢吃啥<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio2"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span>&gt;</span>黄焖鸡<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio2"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>沙县小吃<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>
</code></pre>
</div></demo-box><h2>禁用状态</h2>
<p>不可用状态</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-radio v-model="radio3" :label="1" :disabled="disabled3">禁用</cat-radio>
<cat-radio v-model="radio3" :label="2" :disabled="disabled3">禁用</cat-radio>
<cat-button size="small" type="danger" @click="disabled3 = !disabled3">切换禁用</cat-button>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio3"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabled3"</span>&gt;</span>禁用<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio3"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"2"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabled3"</span>&gt;</span>禁用<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-button</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"disabled3 = !disabled3"</span>&gt;</span>切换禁用<span class="hljs-tag">&lt;/<span class="hljs-name">cat-button</span>&gt;</span>
</code></pre>
</div></demo-box>

</section></template>
<style lang="scss" scoped>
  p {
    padding: 10px 0;
    line-height: 35px;
  }
</style>
<script>
export default {
  data () {
    return {
      radio: '',
      radio2: '',
      radio3: 1,
      radio4: '',
      disabled3: true
    }
  }
}
</script>


